

.header {
  position: fixed ;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 5vh;
    z-index: 1;
    width: 100vw;
    background-color: black;
  }
  .sub-header{
    position: relative;
    top: 90px;
  }
  .menu-links {
    display: flex;
  }
  .insertdata {
    height: 60vh;
    width: 100vw;
  } 
  
  .inside {
    width: 50vw;
    height: 50vh;
    padding: 20px; /* Adjust padding as needed */
  }
  
  .inside h4 {
    margin-bottom: 10px; /* Adjust margin as needed */
  }
  
  .inside input {
    width: 40%;
    padding: 8px;
    margin-bottom: 15px; /* Adjust margin as needed */
    box-sizing: border-box;
    border-radius: 40px;
  }
  
  /* Add any additional styling as needed */
  
  
  .menu-links h3 {
    padding: 10px;
    color: white; 
  }
  
  .menu-links h3:hover {
    transition: background-color 0.3s ease; /* Add a smooth transition effect */
    color:rgb(251, 87, 28);
  }

.prom{
  z-index: 999; }
.dropbtn:hover {
  background-color: rgb(251, 87, 28); /* Change this to the desired background color on hover */
  color: black;
}

  


.navbar {
  overflow: hidden;
  background-color: #333;
}

.navbar a {
  float: left;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;  
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}



.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: rgb(251, 87, 28);
  color: black;
}


.dropdown:hover .dropdown-content {
  display: block;
  color: black;
}

.dropbtn:hover {
  color: rgb(251, 87, 28);
}

@media (max-width: 480px) {
  .header  img {
    height: 50vh; 
  }
  .promr{
    font-size: 10px;
  }
}



